import React from 'react'
import PropTypes from 'prop-types';
import { makeStyles, Typography } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    cover: {
        width: "320px",
        height: "320px"
    },
    albumDiv: {
        margin: "10px",
        borderRadius: "3px",
        width: "320px",
        height: "320px",
        position: "relative",
        display: "inline-block",
    },
    playButton: {
        position: "absolute",
        top: "110px",
        left: "110px",
        backgroundColor: "black",
        opacity: 0,
        '&:hover': {
            opacity: 0.8
        },
        cursor: "pointer",
        borderRadius: "50px",
        width: "100px",
        height: "100px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    },
    playButtonInner: {
        width: "50px",
        color: "white"
    }
}));

export default function AlbumViewComponent(props) {
    const classes = useStyles();
    const { name, id, release_date, artists, external_urls, images } = props.album;
    return (
        <div style={{
            display: "grid",
            gridGap: "20px",
            alignItems: "center",
            gridAutoFlow: "column",
        }}>
            <div key={id} className={classes.albumDiv}>
                <img
                    alt={name}
                    src={images[0].url}
                    width="320px"
                    height="auto"
                    style={{
                        display: "block",
                        borderRadius: "3px"
                    }}
                />
                <div className={classes.playButton}
                    onClick={() => this.onClick(external_urls.spotify)}>
                    <span className={classes.playButtonInner}>Preview</span>
                </div>

            </div>
            <div>
                <Typography>{name}</Typography>
                {artists.map(artist => <Typography>{artist.name}</Typography>)}
                <Typography>{release_date}</Typography>
            </div>
        </div>
    )
}