import React from 'react';
import NestedBackground from './components/background/NestedBackground.jsx'
import { ThemeProvider, createMuiTheme, CssBaseline, responsiveFontSizes } from '@material-ui/core'
import Home from './routes/Home.jsx';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Navbar from './components/navbar/Navbar.jsx';
import Others from './routes/Others.jsx';
import AboutMe from './routes/AboutMe.jsx';
import Releases from './routes/Releases.jsx';

const theme = responsiveFontSizes(createMuiTheme({
  palette: {
    primary: {
      main: '#dd8808',
    },
    secondary: {
      main: '#749fa3',
    },
    contrastThreshold: 3,
    tonalOffset: 0.2,
  },
  typography: {
    fontFamily: 'Times',
  },
}));
const relativeRoute = "music-portfolio";
const routes = {
  home: {
    name: "Home",
    component: Home,
    path: relativeRoute + "/",
  },
  others: {
    name: "Others",
    component: Others,
    path: relativeRoute + "/others"
  },
  aboutMe: {
    name: "About Me",
    component: AboutMe,
    path: relativeRoute + "/about-me"
  },
  releases: {
    name: "Releases",
    component: Releases,
    path: relativeRoute + "/releases"
  },
}
function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline/>
      <BrowserRouter>
        <NestedBackground navbar={<Navbar routes={routes}/>}>
          
          <Switch>
            <Route exact path={routes.home.path} component={routes.home.component} />
            <Route path={routes.aboutMe.path} component={routes.aboutMe.component} />
            <Route path={routes.others.path} component={routes.others.component} />
            <Route path={routes.releases.path} component={routes.releases.component} />
          </Switch>

        </NestedBackground>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;